    display: block;
  }

  .lg\:flex {
    display: flex;
  }

  .lg\:hidden {
    display: none;
  }

  .lg\:w-\[250px\] {
    width: 250px;
  }

  .lg\:w-full {
    width: 100%;
  }

  .lg\:justify-center {
    justify-content: center;
  }

  .lg\:p-0 {
    padding: 0px;
  }

  .lg\:p-10 {
    padding: 2.5rem;
  }
}
